body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

#root{position:relative;min-height:100vh;padding-bottom:4rem}.text-maroon{color:#b00000}a.text-maroon:hover{color:#900000}.bg-maroon{background-color:#b00000}button.bg-maroon:hover{background-color:#900000}nav img{height:2rem}nav ul li{padding-left:5px}nav ul li a{border-radius:5px;border:3px solid transparent}nav ul li a.active{border:3px solid #777777}nav ul li a:hover{border:3px solid #777777}.nav-tabs a{color:#A9A9A9;cursor:pointer}.nav-tabs a.active{font-weight:bold}.board-list{list-style-type:none;margin-top:1rem;padding:0;text-align:center;font-size:20px;font-weight:bold}.event-date{position:absolute;left:10%;top:-7.5%;width:3.5rem}.event-date .event-month{height:1.5rem;background:#696969;line-height:1.5rem}.event-date .event-day{height:2.5rem;background:#f4b35b;line-height:2.5rem;text-align:center;font-size:2rem;font-weight:bold}.event-info span{margin-left:0.5rem}.event-info .info-secondary{margin-left:3rem}.description-secondary{color:#919191;margin-left:1.5rem}#contactInfo{list-style-type:none;text-align:left;font-size:20px;font-weight:bold;margin-top:16px}.fade-in{opacity:0;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}@media screen and (max-width: 575px){#root{padding-bottom:9rem}}

